import React from 'react'
import { makeStyles, withStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import HomeIcon from '@material-ui/icons/Home';
import VideocamIcon from '@material-ui/icons/Videocam';
import NotificationsIcon from '@material-ui/icons/Notifications';
const NavigationWrapper = withStyles({
    root: {
      backgroundColor: '#333333',
      justifyContent:'left',
      color: 'white',
    },
  })(BottomNavigation);
  const NavigationAction = withStyles({
    root: {
      color: 'white',
    },
})(BottomNavigationAction);
const HomeFooter = ({mode}) => {
    const navigationMode = mode !== 0
    return (
        <NavigationWrapper style={{width: "100%"}} showLabels={true} >
            {
              navigationMode && 
              <NavigationAction href='/home' label="" value="home" icon={<HomeIcon />} />
            }
            {
              navigationMode && 
              <NavigationAction href='/live' label="" value="live" icon={<VideocamIcon />} />
            }
            {
              navigationMode && 
              <NavigationAction href='/notify' label="" value="notification" icon={<NotificationsIcon />} />
            }
        </NavigationWrapper>
    )
}

export default HomeFooter