import React, { Component } from "react";
import withStyles from "@material-ui/styles/withStyles";
import Paper from '@material-ui/core/Paper';
import ListCard from './base/ListCard';
import BasePanel from 'components/base/BasePanel';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar';
import EmojiEmotionsIcon from '@material-ui/icons/EmojiEmotions';
import EmojiEmotionlessIcon from './base/icons/EmojiEmotionlessIcon';
import EmojiEmotionsadIcon from './base/icons/EmojiEmotionsadIcon';
import Box from '@material-ui/core/Box';
import * as homeActions from 'redux/modules/homePage';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import {
  SHOW_FOLLOWPANEL,
  SHOW_PPROUCTSPANEL,
  SHOW_TRANSACTIONSPANEL,
  SHOW_LIVELISTPANEL,
  SHOW_PADDLINK,
  SHOW_SELLHISTORYLISTPANEL,
  SHOW_CHANGEEMAILPANEL,
  SHOW_CHANGEEPASSWORDPANEL,
  SHOW_STOREMANAGEMENT,
  SHOW_PRIVACY,
  SHOW_POLICY
} from 'lib/constant'

const styles = theme => ({
  root: {
    height: '100%',
    padding: 0,
    background: '#F5F5F5'
  },
  avatarWrapper: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    width: '100%',
    margin: 0,
    paddingTop: '24px',
    paddingBottom: '17px',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
    width: '80px',
    height: '80px',
    borderRadius: '40px'
  },
  card: {
    marginTop: 50
  },
  cardDetails: {
    flex: 1,
    display: 'flex',
    padding: 0,
  },
  followList: {
    marginTop: 50,
    padding: 20
  },
  followDetails: {
    margin: 0,
    padding: 0,
    borderTopWidth: '0px'
  },
  childToCenter: {
    margin: 'auto'
  },

  userName: {
    fontFamily: 'Noto Sans JP',
    fontStyle: 'normal',
    fontWeight: '500',
    fontSize: '17px',
    lineHeight: '100%',
  },

  evaluation: {
    fontFamily: 'Noto Sans JP',
    fontStyle: 'normal',
    fontWeight: 'normal',
    fontSize: '13px',
    lineHeight: '100%',
  },

  emotion: {
    fontFamily: 'Noto Sans JP',
    fontStyle: 'normal',
    fontWeight: '500',
    fontSize: '10px',
    lineHeight: '100%',
    color: '#A5A5A5'
  },

  emotionCount: {
    fontFamily: 'Roboto',
    fontStyle: 'normal',
    fontWeight: 500,
    fontSize: '15px',
    lineHeight: '100%',
  },

  thumbnail: {
    width: '80px',
    height: '80px'
  }

});

class HomePanel extends Component {
  render() {
    const { classes, HomeActions, mode } = this.props;
    let userInfo = this.props.userInfo
    //if(userInfo != null && userInfo != undefined) userInfo = userInfo.toJS()
    const handleClick = (panelNumber, panelType) => {
      switch (panelType) {
        case 1:
          HomeActions.changeFirstStatus(panelNumber)
          HomeActions.changeSecondStatus(0)
          HomeActions.changeThirdStatus(0)
          break;
        case 2:
          if(SHOW_PPROUCTSPANEL === panelNumber)
          {
            HomeActions.changeThirdStatus(SHOW_PADDLINK)
          }
          HomeActions.changeSecondStatus(panelNumber)
          break;
        case 3:
          HomeActions.changeThirdStatus(panelNumber)
          break;
        default:
          break;
      }

    }
    
    let like =  0, dislike = 0, notBad = 0
    if(userInfo != null)
    {
      if (userInfo.evaluation != null) {
        like = userInfo.evaluation.like;
        dislike = userInfo.evaluation.dislike;
        notBad = userInfo.evaluation.notBad;
      }
    }
    return (
      <BasePanel mode={mode}>

        <Grid xs={12} item>
          <Paper variant="outlined" square elevation={1} className={classes.avatarWrapper}>
            <Avatar className={classes.avatar} src={userInfo.thumbnail}></Avatar>
            <Typography component="h1" variant="h5" className={classes.userName}>
              {userInfo.nickname}
            </Typography>
          </Paper>
        </Grid>
        {/* /.Avatar */}

        {/* Link to Follow List */}
        <Grid item style={{ marginTop: '35px' }} xs={12} >
          <ListCard text={''}
            number={userInfo.numFollowers}
            panelNumber={SHOW_FOLLOWPANEL}
            panelLocation={2}
            handleClick={handleClick} />
        </Grid>
        {/* Link to Follow List */}
        {/* Follow Detail */}
        <Grid item style={{ paddingBottom: '40px' }} xs={12}>
          <Paper variant="outlined" square className={classes.followDetails}>
            <Grid container style={{ padding: '0 16px' }}>
              <Grid xs={4} className={classes.childToCenter} item>
                <Typography component="h4">
                  <Box textAlign="left" className={classes.evaluation}></Box>
                </Typography>
              </Grid>
              <Grid xs={2} item></Grid>
              <Grid xs={2} item>
                <Typography component="div">
                  <Box lineHeight={1} mt={1.5}><EmojiEmotionsIcon style={{ verticalAlign: 'text-top', color: '#4BD458' }} /></Box>
                  <Box lineHeight={1} mt={0.2} className={classes.emotion}></Box>
                  <Box lineHeight={1} mt={0.75} mb={1.5} className={classes.emotionCount}>{like}</Box>
                </Typography>
              </Grid>
              <Grid xs={2} item>
                <Typography component="div">
                  <Box lineHeight={1} mt={1.5}><EmojiEmotionlessIcon style={{ verticalAlign: 'text-top', color: '#BDBDBD' }} /></Box>
                  <Box lineHeight={1} mt={0.2} className={classes.emotion}></Box>
                  <Box lineHeight={1} mt={0.75} mb={1.5} className={classes.emotionCount}>{notBad}</Box>
                </Typography>
              </Grid>
              <Grid xs={2} item>
                <Typography component="div">
                  <Box lineHeight={1} mt={1.5}><EmojiEmotionsadIcon style={{ verticalAlign: 'text-top', color: '#D74936' }} /></Box>
                  <Box lineHeight={1} mt={0.2} className={classes.emotion}></Box>
                  <Box lineHeight={1} mt={0.75} mb={1.5} className={classes.emotionCount}>{dislike}</Box>
                </Typography>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
        {/* Follow Detail */}
        <Grid item xs={12} >
          <ListCard text={''}
            panelNumber={SHOW_PPROUCTSPANEL}
            panelLocation={2}
            handleClick={handleClick} />
        </Grid>
        <Grid item xs={12} >
          <ListCard text={''}
            panelNumber={SHOW_TRANSACTIONSPANEL}
            panelLocation={2}
            handleClick={handleClick} />
        </Grid>
        <Grid item xs={12} >
          <ListCard text={''}
            panelNumber={SHOW_SELLHISTORYLISTPANEL}
            panelLocation={2}
            handleClick={handleClick} />
        </Grid>
        <Grid item xs={12} >
          <ListCard text={''}
            panelNumber={SHOW_LIVELISTPANEL}
            panelLocation={2}
            handleClick={handleClick} />
        </Grid>
        <Grid item xs={12} >
          <ListCard text={''}
            panelNumber={SHOW_STOREMANAGEMENT}
            panelLocation={2}
            handleClick={handleClick} />
        </Grid>

        <Grid item style={{ paddingTop: '40px' }} xs={12} >
          <ListCard text={''}
            panelNumber={SHOW_CHANGEEMAILPANEL}
            panelLocation={2}
            handleClick={handleClick} />
        </Grid>
        <Grid item xs={12} >
          <ListCard text={''}
            panelNumber={SHOW_CHANGEEPASSWORDPANEL}
            panelLocation={2}
            handleClick={handleClick} />
        </Grid>

        <Grid item style={{ paddingTop: '40px' }} xs={12} >
          <ListCard text={''}
            panelNumber={SHOW_PRIVACY}
            panelLocation={2}
            handleClick={handleClick} />
        </Grid>
        <Grid item xs={12} >
          <ListCard text={''}
            panelNumber={SHOW_POLICY}
            panelLocation={2}
            handleClick={handleClick} />
        </Grid>
        <Grid item xs={12} style={{ paddingBottom: '40px' }} >
          <ListCard text={''}
            handleClick={handleClick} />
        </Grid>

      </BasePanel>
    );
  }
}
export default connect(
  (state) => ({
    firstPanelVisible: state.homePage.get('firstPanel'),
    secondPanelVisible: state.homePage.get('seconPanel'),
    thirdPanelVisible: state.homePage.get('thirdPanel'),
    userInfo: state.user.get('userInfo')
  }),
  (dispatch) => ({
    HomeActions: bindActionCreators(homeActions, dispatch),
  })
)(withStyles(styles)(HomePanel));
